function Sliders() {
    let SlidersArray = []

    let sliders = document.querySelectorAll(".slider")
    sliders.forEach((slider,index) => {
        slider.setAttribute("data-slider-id",index)
        let isLoop = slider.classList.contains("_loop")
        let isAutoplay = slider.classList.contains("_autoplay")

        SlidersArray[index] = new Swiper(`.slider[data-slider-id="${index}"] .swiper-container`, {
            slidesPerView: "auto",
            spaceBetween: 0,
            watchSlidesProgress: true,
            preventClicks :true,
            a11y: false,
            observer: true,
            observeParents: true,
            observeSlideChildren: true,
            loop: isLoop,
            autoplay: isAutoplay,
            navigation: {
                nextEl: `.slider[data-slider-id="${index}"] .swiper-button-next`,
                prevEl: `.slider[data-slider-id="${index}"] .swiper-button-prev`,
            },
            pagination: {
                el: `.slider[data-slider-id="${index}"] .swiper-pagination`,
                type: 'bullets',
                clickable: true,
            },
        })
    })
}

function mainSlider() {
    let mainBtn = document.querySelector(".main__btn")

    var swiper = new Swiper('.main-slider .swiper-container', {
        slidesPerView: "auto",
        spaceBetween: 0,
        direction: 'vertical',
        loop: true,
        mousewheel: {
            releaseOnEdges: true,
        },
        on: {
            init: function () {
                setActiveSlide()
            },
        },
    })
    swiper.on('slideChangeTransitionEnd', function () {
        setActiveSlide()
    });

    function setActiveSlide() {
        let activeSlideLink = document.querySelector(".main-slider .swiper-slide-active .main__link")
        mainBtn.setAttribute(
            "href",
            activeSlideLink.getAttribute("href")
            )
    }
}


Sliders()
mainSlider()
